/* ==================== LAYOUT PRINCIPAL ==================== */
.admin-layout {
  display: flex;
  min-height: 100vh;
  background-color: #f5f7fa;
}

/* ==================== SIDEBAR ==================== */
.admin-sidebar {
  width: 260px;
  background: linear-gradient(180deg, #2c3e50 0%, #34495e 100%);
  color: white;
  display: flex;
  flex-direction: column;
  position: fixed;
  height: 100vh;
  left: 0;
  top: 0;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
}

.sidebar-header {
  padding: 24px 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.sidebar-header h2 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 4px;
}

.sidebar-header p {
  font-size: 0.85rem;
  opacity: 0.8;
}

/* Navegación */
.sidebar-nav {
  flex: 1;
  padding: 20px 0;
  overflow-y: auto;
}

.nav-item {
  width: 100%;
  padding: 14px 20px;
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.8);
  text-align: left;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s ease;
  border-left: 3px solid transparent;
}

.nav-item:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.nav-item.active {
  background: rgba(255, 255, 255, 0.15);
  color: white;
  border-left-color: #3498db;
}

/* Footer Sidebar */
.sidebar-footer {
  padding: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.user-info {
  margin-bottom: 12px;
}

.user-name {
  font-weight: 600;
  font-size: 0.95rem;
  margin-bottom: 4px;
}

.user-role {
  font-size: 0.8rem;
  opacity: 0.7;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.btn-logout {
  width: 100%;
  padding: 10px;
  background: rgba(231, 76, 60, 0.2);
  border: 1px solid rgba(231, 76, 60, 0.5);
  color: white;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn-logout:hover {
  background: #e74c3c;
  border-color: #e74c3c;
}

/* Main Content */
.admin-main {
  flex: 1;
  margin-left: 260px;
  padding: 30px;
  overflow-y: auto;
}

/* ==================== LOADING ==================== */
.loading-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-top-color: white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.loading-container p {
  font-size: 1.2rem;
  font-weight: 600;
}

/* ==================== RESPONSIVE ==================== */
@media (max-width: 768px) {
  .admin-sidebar {
    width: 70px;
  }

  .sidebar-header h2,
  .sidebar-header p,
  .nav-item span,
  .user-info,
  .btn-logout span {
    display: none;
  }

  .admin-main {
    margin-left: 70px;
  }
}

/* ==================== ANIMACIONES ==================== */
.admin-main {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Notification badge en sidebar */
.nav-item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.notification-badge {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  background: #e74c3c;
  color: white;
  border-radius: 12px;
  padding: 3px 8px;
  font-size: 0.75rem;
  font-weight: bold;
  min-width: 20px;
  text-align: center;
  animation: badgePulse 2s ease-in-out infinite;
}

@keyframes badgePulse {
  0%, 100% {
    transform: translateY(-50%) scale(1);
    box-shadow: 0 0 0 0 rgba(231, 76, 60, 0.7);
  }
  50% {
    transform: translateY(-50%) scale(1.05);
    box-shadow: 0 0 0 6px rgba(231, 76, 60, 0);
  }
}